body {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  margin: 70px auto 0;
  padding: 0;
  display: flex;
  position: relative;
}

.stopwatch {
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
  position: absolute;
}

.stopwatch__min {
  transform-origin: bottom;
  background: #0700ff;
  width: 3vmin;
  height: 20vmin;
  margin-bottom: 20vmin;
  animation: 3600s steps(60, end) infinite relogio;
  position: absolute;
  bottom: 30%;
}

.stopwatch__sec {
  transform-origin: bottom;
  background: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation-name: relogio;
  animation-duration: 60s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  position: absolute;
  bottom: 50%;
}

.stopwatch__center {
  background: #f6a603;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 5vmin;
  height: 5vmin;
  display: flex;
  position: absolute;
}

.stopwatch--speed-up .stopwatch__sec {
  transform-origin: bottom;
  animation-name: relogio;
  animation-duration: 10s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}

.stopwatch--speed-up .stopwatch__min {
  transform-origin: bottom;
  animation: 600s steps(60, end) infinite relogio;
}

@keyframes relogio {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}
